.main-body {
  margin: 0;
  padding: 0;
  font-family: Scada, sans-serif;
}
.wrapper {
  padding: 0 10% 0 10%;
}
.main-header {
  color: #ffff;
  display: flex;
  padding: 0 10% 0 10%;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  background: #f6cb1f;
  background: linear-gradient(90deg, rgba(246, 203, 31, 0.88308827) 15%, rgba(244, 228, 53, 0.90549723) 75%);
  height: 75px;
}
.main-header__profile {
  width: auto;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}
.main-header__heading {
  font-size: 24px;
}
.main-header__username {
  font-size: 20px;
  padding: 15px;
  color: #FFFFFF;
  text-decoration: none;
}
.main-header__photo {
  border: 3px solid #FFFFFF;
  margin: 0 0 0 10px;
  width: 55px;
  height: 55px;
  border-radius: 50%;
}
.main-menu__list {
  padding: 0 0 0 10%;
  width: 450px;
  list-style: none;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  background-color: white;
}
@media (max-width: 320px) {
  .main-menu__list {
    width: 90%;
    padding: 0 10% 0 10%;
  }
}
.main-menu__item {
  padding: 0;
  margin: 0;
}
.main-menu__item--active {
  border-bottom: 2px solid #0F0C34;
}
.default {
  text-decoration: none;
  color: #D4D4D4;
  font-size: 24px;
  text-transform: uppercase;
  font-weight: bold;
}
@media (max-width: 320px) {
  .default {
    font-size: 16px;
  }
}
.land {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
}
.land-header {
  padding-top: 50px;
  background-size: 80%;
  background-repeat: no-repeat;
  background-position: center;
}
.land-header__main {
  margin-top: 50px;
  margin-bottom: 200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
@media (min-width: 1200px) {
  .land-header__main {
    flex-wrap: nowrap;
  }
}
.land-header__container {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0 10% 0 5%;
}
.land-header__photo {
  width: 100%;
  margin: 10% 0 10% 0;
}
@media (min-width: 1200px) {
  .land-header__photo {
    margin: 0;
  }
}
.land-header__heading {
  font-size: 23px;
  margin: 5%;
}
@media (min-width: 1200px) {
  .land-header__heading {
    position: absolute;
    font-size: 63px;
    margin: 50px 0 0 40px;
  }
}
.land-header__button {
  text-decoration: none;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 80%;
  padding: 10px;
  font-size: 16px;
  background: linear-gradient(90deg, #ffa703 15%, #eeec29 85%);
  border: none;
  font-weight: bold;
  text-transform: uppercase;
  color: #FFFFFF;
}
@media (min-width: 1200px) {
  .land-header__button {
    font-size: 30px;
    width: 500px;
    padding: 20px;
    text-align: center;
  }
}
.land-header__button--register {
  display: none;
}
.land-header__add-container {
  text-align: center;
  margin: 20px 10% 0 10%;
  background-color: #F3F3F3;
  padding: 10px;
  border-radius: 10%;
}
@media (min-width: 1200px) {
  .land-header__add-container {
    width: 20vw;
    margin: 0;
  }
}
.search {
  width: 1130px;
  display: block;
  margin-left: 10%;
  padding-bottom: 20px;
}
@media (max-width: 321px) {
  .search {
    width: 100%;
    margin: 0 10% 0 10%;
  }
}
.search__title {
  font-size: 24px;
  color: #A4A3A3;
}
.search__subject {
  display: flex;
  flex-wrap: nowrap;
  width: auto;
  align-items: center;
}
.search__subject-title {
  text-align: left;
  font-size: 20px;
  font-weight: bold;
}
@media (max-width: 320px) {
  .search__subject-title {
    text-align: center;
  }
}
.search__subject-photo {
  width: 4vw;
  margin: 0 20px 0 0;
}
.search__subjects-list {
  list-style: none;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}
.search__subject {
  border-radius: 10px;
}
.search__age-title {
  margin-left: 100px;
}
.search__container {
  display: flex;
  justify-content: space-around;
}
@media (max-width: 321px) {
  .search__container {
    flex-wrap: wrap;
  }
}
.search__input {
  width: 250px;
  font-size: 20px;
}
.search__input--region {
  width: 350px;
}
@media (max-width: 321px) {
  .search__input--region {
    width: 80%;
  }
}
.search__label {
  font-size: 23px;
}
.people {
  background-color: #7de3b0;
}
.people__list {
  padding: 10px 10% 0 10%;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
.people__item {
  display: flex;
  flex-wrap: nowrap;
  margin: 0 0 30px 0;
  width: 1200px;
  background-color: #FFFFFF;
  height: 340px;
  text-decoration: none;
  color: #000000;
}
@media (max-width: 321px) {
  .people__item {
    width: 90%;
    flex-wrap: wrap;
    height: auto;
  }
}
.people__item:hover {
  background-color: #FFFFFF;
  cursor: pointer;
  transform: scale(1.02);
}
.people__info-container {
  padding: 0 0 0 20px;
}
@media (max-width: 321px) {
  .people__info-container {
    text-align: center;
  }
}
.people__description {
  color: #868686;
  font-size: 24px;
  padding: 0 20px 0 0;
}
@media (max-width: 321px) {
  .people__description {
    font-size: 14px;
  }
}
.people__title {
  padding: 0 10px 0 0;
  font-size: 36px;
  margin: 10px 0 0 0;
}
@media (max-width: 321px) {
  .people__title {
    text-align: center;
    font-size: 15px;
  }
}
.people__age {
  font-size: 24px;
  font-weight: bold;
}
@media (max-width: 321px) {
  .people__age {
    font-size: 15px;
  }
}
.people__photo {
  margin: 0;
}
@media (max-width: 321px) {
  .people__photo {
    width: 100%;
  }
}
.people__price {
  padding: 4px;
  border-radius: 5px;
  background-color: #C4C4C4;
  color: #FFFFFF;
  font-size: 10px;
}
.people__container {
  padding-left: 10px;
  margin-bottom: 5px;
  width: 140px;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
}
.people__button {
  padding: 4px;
  border-radius: 3px;
  font-size: 20px;
  font-weight: bold;
  background-color: #4DE55D;
  color: #ffff;
  border: none;
}
@media (max-width: 320px) {
  .people__button {
    font-size: 12px;
  }
}
.people__location-container {
  width: 150px;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
}
.people__location-title {
  font-size: 24px;
  color: #A4A3A3;
}
.people__location-icon {
  width: 40px;
  color: #A4A3A3;
}
.person {
  display: flex;
  flex-wrap: wrap;
  padding: 20px 10% 0 10%;
}
.person__list {
  margin: 5px;
  padding: 0;
  list-style: none;
}
.person__img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 20%;
}
.person__info-container {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}
@media (max-width: 320px) {
  .person__info-container {
    flex-wrap: wrap;
  }
}
.person__photo {
  width: 25%;
}
@media (max-width: 320px) {
  .person__photo {
    width: 90%;
  }
}
.person__skill {
  border-radius: 20px;
  margin-top: 25px;
  width: 100%;
  text-align: center;
  background-color: #F3F3F3;
  padding-top: 10px;
}
.search__subject--person {
  transform: translateX(calc(100% - 60%));
}
@media (max-width: 320px) {
  .search__subject--person {
    transform: translateX(0);
  }
}
.person__skill-title {
  font-size: 25px;
}
.person__description {
  padding: 0 10% 0 10%;
  font-size: 20px;
}
.person__info {
  text-align: center;
}
.person__skills-info {
  display: flex;
  flex-wrap: nowrap;
}
@media (max-width: 320px) {
  .person__skills-info {
    flex-wrap: wrap;
  }
}
.person__skills-title {
  text-align: center;
}
.person__skills-wrapper {
  flex-grow: 2;
}
.person__button {
  font-size: 25px;
  font-weight: bold;
  text-transform: uppercase;
  border: none;
  color: #ffff;
  padding: 20px;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  background: #f6cb1f;
  background: linear-gradient(90deg, rgba(246, 203, 31, 0.88308827) 15%, rgba(244, 228, 53, 0.90549723) 75%);
  height: 75px;
}
.registration-form {
  display: flex;
  flex-wrap: wrap;
  padding: 0 20% 0 20%;
  font-size: 24px;
}
.registration-form__container {
  width: 100%;
  margin: 10px;
  display: flex;
  align-items: center;
}
.order-form__container--description {
  display: flex;
  flex-wrap: wrap;
}
.registration-form__input {
  width: 250px;
  height: 25px;
  font-size: 24px;
  padding: 10px;
  margin-left: 20px;
}
.registration-form__subjects-title {
  width: 100%;
}
.registration-form__textarea {
  font-size: 24px;
  width: 100%;
  height: 200px;
}
.registration-form__container--subjects {
  flex-wrap: wrap;
}
